.home-container{
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;
}

.section{
    display: flex;
    align-items:center ;
    width: 100%;
    justify-content: center;
}

.home__container{
    display: flex;
    gap: 1rem;
    align-items: center;
    justify-content: center;
    width: 70%;
}

.home{
    width: 20%;
    height: 20%;
}

.home__item{
    background: rgb(199, 0, 57, 0.06);
    padding: 1.3rem;
    border-radius: 2rem;
    border: 1px solid transparent;
}

img{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30%;
    height: 30%;
}

.home__item:hover{
    border-color: black;
    background: transparent;
}

.home__item-image{
    border-radius: 1.5rem;
    overflow: hidden;
}

.home__item h3{
    margin: 1.2rem 0 2rem;
}

.home__item-cta{
    display: flex;
    gap: 1rem;
    margin-bottom: 1rem;
}

.home-des{
    width: 15rem;
    height: 5rem;
}

